<template>
    <div class="charge main-bg">
        <van-form @submit="handleGoCharge" :show-error-message="false">
            <van-field
                class="radius-10"
                type="number"
                v-model="inHosNo"
                placeholder=""
                :rules="[{ required: true, message: '' }]"
                center
                clearable
            />
            <div class="tips">
                <span class="strong"></span><span></span>
            </div>
            <base-button class="m-t-20" text="" native-type="submit" :btnDisabled="btnDisabled" size="large" />
        </van-form>
    </div>
</template>

<script>
import BButton from "../../base/BButton";
export default {
    name: "QueryOthersInHosNo",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            inHosNo: "",
            btnDisabled: false
        };
    },
    methods: {
        handleGoCharge() {
            let systemType = this.globalMethods.getCookie("systemType");
            let payType = systemType == "0A" ? "" : "";
            this.$dialog.confirm({
                title: '',
                confirmButtonText: "",
                message: `${payType},${payType}`,
            })
            .then(() => {
                this.queryPatientInfoByID();
            })
            .catch(() =>{});
        },
        queryPatientInfoByID() {
            this.btnDisabled = true;
            this.$api.inHosNo.queryPatientInfoByID({
                data: {
                    inHosNo: this.inHosNo,
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: {
                    forbidClick: false,
                },
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.$router.push({name: "ChargeDeposit", query: {inHosNo: this.inHosNo}});
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            });
        }
    }
};

</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: blue;
}

</style>



