import axios from 'axios';
import { Toast } from 'vant';


//  
// 
const loading = (forbidClick = true) => {
    Toast.loading({
        message: '...',
        duration: 0,
        forbidClick
    });
}

export function all (...request) {
    loading();
    return axios.all(request).then(axios.spread(function (...response) {
        // 
        Toast.clear();
        return response;
    }));
}


