<template>
    <div class="container main-bg">
        <van-form @submit="handleSubmit" :show-error-message="false">
        <!--  -->
        <van-field v-model="userType" name="userType" v-show="false"></van-field>
        <van-tabs 
            class="user-type radius-10" 
            v-model="isChild"
            title-inactive-color="#818594"
            title-active-color="#292C34"
            v-show="!authCode"
            >
            <van-tab title="">
                <template #title>
                    <span class="custom-tab-title"></span>
                </template>
            </van-tab>
            <van-tab title=""></van-tab>
        </van-tabs>
        <div class="section radius-10">
            <!--  -->
            <van-field
                name="patName"
                v-model="patName"
                label=""
                label-width="120"
                :placeholder="isChild ? '' : ''"
                :rules="[{ required: true, message: '' }]"
                :readonly="!!authCode"
                clearable
            ></van-field>
            <!--  -->
            <van-field
                name="patSex"
                label=""
                label-width="120"
                v-if="isChild"
                >
                    <template #input>
                        <van-radio-group v-model="patSex" direction="horizontal">
                            <van-radio name="1"></van-radio>
                            <van-radio name="2"></van-radio>
                        </van-radio-group>
                    </template>
            </van-field>
            <!--  -->
            <template v-if="isChild">
                <van-field
                    name="patBirthday"
                    v-model="patBirthday"
                    label=""
                    label-width="120"
                    placeholder=""
                    @click-input="handleSelectDate"
                    :rules="[{ required: true, message: '' }]"
                    readonly
                ></van-field>
                <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
                    <van-datetime-picker
                    v-model="currentDate"
                    type="date"
                    title=""
                    :min-date="minDate"
                    :max-date="maxDate"
                    @confirm="handleDateComfirm"
                    @cancel="handleDateCancel"
                    />
                </van-popup>
            </template>
            <!--  -->
            <template v-if="!isSelf">
                <van-field
                    name="patRelation"
                    v-model="patRelation"
                    label=""
                    label-width="120"
                    placeholder=""
                    @click="handleSelectRelation"
                    :rules="[{ required: true, message: '' }]"
                    readonly
                ></van-field>
                <van-popup v-model="showRelationPicker" round position="bottom">
                    <van-picker
                    show-toolbar
                    :columns="columns"
                    @cancel="showRelationPicker = false"
                    @confirm="handleConfirm"
                    />
                </van-popup>
            </template>
            <!--  -->
            <van-field
                name="guardianName"
                v-model="guardianName"
                label=""
                label-width="120"
                placeholder=""
                :rules="[{ required: true, message: '' }]"
                clearable
                v-if="isChild"
            ></van-field>
            <!--  -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                :label="isChild ? '' : ''"
                :placeholder="isChild ? '' : ''"
                label-width="120"
                maxlength="18"
                :rules="[{ required: true, message: '' }]"
                :readonly="!!authCode"
                clearable
            ></van-field>
            <!--  -->
            <van-field
                name="ybCardNo"
                v-model="ybCardNo"
                label=""
                placeholder=""
                label-width="120"
                maxlength="18"
                :rules="[{ required: true, message: '' }]"
                v-if="authCode"
                readonly
                clearable
            ></van-field>
            <!--  -->
            <van-field
                name="patPhone"
                type="number"
                v-model="patPhone"
                :label="isChild ? '' : ''"
                :placeholder="isChild ? '' : ''"
                label-width="120"
                maxlength="11"
                :rules="[{ required: true, message: '' }]"
                clearable
            ></van-field>
        </div>
        <div class="section radius-10">
            <!--  -->
            <van-field 
                name="switch" 
                label="" 
                label-width="120"
                >
                    <template #input>
                        <van-switch v-model="isSelf" size="20" />
                    </template>
            </van-field>
        </div>
        <!--  -->
        <div class="tips">
            <p class="normal m-b-5"></p>
            <p class="normal">1 <span class="danger"></span> </p>
            <p class="normal">2<span class="danger"></span> </p>
        </div>
        <base-button text="" size="large" native-type="submit" :btnDisabled="btnDisabled" />
    </van-form>
    <van-popup class="select-card-modal radius-10" v-model="showPopup" @close="handleClosePopup">
        <div class="title"></div>
        <div class="popup">
            <div class="content">
                <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="bindCard(item.patId)">
                    <div class="wrap">
                        <div class="row">
                            <span class="name">{{item.patName}}</span>
                        </div>
                        <div class="row card-code">
                            <span> :</span>
                            <span>{{item.patMedNo}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </van-popup>
  </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "BindCard",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            isChild: 0,
            patName: "",
            patSex: "1",
            currentDate: new Date(),
            minDate: new Date(1990, 0, 1),
            maxDate: new Date(2030, 11, 31),
            showDatePicker: false,
            patBirthday: "",
            isSelf: true,
            guardianName: "",
            patRelation: "",
            showRelationPicker: false,
            columns: ["", "", "", "", "", "", ""],
            patIdNo: "",
            //
            ybCardNo: "",
            //
            noSeqNo: "",
            patPhone: "",
            showPopup: false,
            btnDisabled: false,
            cardList: [],
            formData: {},
            hisDataList: [],
        };
    },
    computed: {
        userType() {
            return this.isChild;
        },
        allowCreatedCard() {
            return this.$store.state.allowCreatedCard
        },
        sociaSecuCard() {
            return this.$store.state.sociaSecuCard;
        },
        newCardSuccessTips() {
            return this.$store.state.newCardSuccessTips;
        },
        authCode() {
            let authCode = null;
            let href = window.location.href;
            if (href.includes("authCode")) {
                let search = href.split("?")[1].split("&");
                for (let item of search) {
                    if (item.includes("authCode")) {
                        authCode = item.split("=")[1];
                        break;
                    }
                }
            }
            return authCode;
        }
    },
    mounted() {
        if (this.authCode) {
            this.$api.outpatient.getSociaSecuCardInfo({
                data: {
                    code: this.authCode
                }
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    this.patName = data.name;
                    this.patIdNo = data.cardId;
                    this.ybCardNo = data.cardNo;
                    this.noSeqNo = data.noSeqNo;
                }
            })
        }
    },
    methods: {
        handleSubmit(values) {
            let formData = values;
            //
            if (!this.validForm(formData)) {
                return;
            };
            this.formData = formData;
            this.btnDisabled = true;
            // 
            if (this.authCode) {
                this.bindSociaSecuCard();
            }
            else {
                this.queryCardList();
            }
        },
        queryCardList() {
            let data = {};
            let formData = this.formData;
            let userType = formData.userType;
            data["patName"] = formData.patName;
            if (userType) {
                data["guardianIdNo"] = formData.patIdNo;
                data["guardianPhone"] = formData.patPhone;
            }
            else {
                data["patIdNo"] = formData.patIdNo;
                data["patPhone"] = formData.patPhone;
            }
            this.$api.outpatient.queryCardList({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    this.hisDataList = resData;
                    if (resData.length == 1) {
                        let patId = resData[0].patId;
                        this.bindCard(patId);
                    }
                    else {
                        this.cardList = resData;
                        this.showPopup = true;
                    }
                }
                else if (response.data.Code == 1) {
                    if (this.allowCreatedCard) {
                        this.$dialog.confirm({
                            title: '',
                            message: '',
                        })
                        .then(() => {
                            this.newCard();
                        })
                        .catch(() =>{
                            this.btnDisabled = false;
                        })
                    }
                    else {
                        this.$dialog.alert({
                            title: '',
                            message: ''
                        })
                        .then(() => {
                            this.btnDisabled = false;
                        })
                    }
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        handleSelectDate() {
            this.closeAllPicker();
            this.showDatePicker = true;
        },
        handleSelectRelation() {
            this.closeAllPicker();
            this.showRelationPicker = true;
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleDateComfirm(value) {
            let year = value.getFullYear();
            let month = (Number(value.getMonth()) + 1) + "";
            month = month.length == 1 ? ("0" + month) : month;
            let day = value.getDate() + "";
            day = day.length == 1 ? ("0" + day) : day;
            this.patBirthday = year + "-" + month + "-" + day;
            this.showDatePicker = false;
        },
        handleConfirm(value) {
            this.patRelation = value;
            this.showRelationPicker = false;
        },
        handleClosePopup() {
            this.btnDisabled = false;
        },
        bindSociaSecuCard() {
            let data = {};
            let formData = this.formData;
            data["ybCardNo"] = formData.ybCardNo;
            data["ybCardSeq"] = this.noSeqNo;
            this.newCard(data);
        },
        bindCard(patId) {
            let owner = this;
            let formData = this.formData;
            let hisData;
            for (let i = 0; i < owner.hisDataList.length; i++) {
                let item = owner.hisDataList[i];
                if (item.patId == patId) {
                    hisData = item;
                    break;
                }
            };
            let data = owner.getParams(hisData, formData);
            data["patId"] = patId;
            data["patMedNo"] = hisData.patMedNo;
            this.queryBind(data);
        },
        queryBind(data) {
            this.$api.outpatient.bindCard({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("binded", true);
                    this.toastSuccess("");
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        newCard(obj) {
            let data = this.getParams("", this.formData);
            if (obj) {
                data = Object.assign(data, obj);
            };
            this.$api.outpatient.createCard({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("binded", true);
                    this.toastSuccess(this.newCardSuccessTips || "");
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg)
                }
            })
        },
        getParams(hisData, formData) {
            let data = {};
            let userType = formData.userType;
            data["isChild"] = userType;
            data["patRelation"] = hisData.patRelation || formData.patRelation || "";
            data["patName"] = hisData.patName || formData.patName || "";
            data["patIdNo"] = !userType ? (hisData.patIdNo || formData.patIdNo) : "";
            data["patPhone"] = !userType ? (hisData.patPhone || formData.patPhone) : "";
            data["patSex"] = hisData.patSex || formData.patSex || "";
            data["patBirthday"] = hisData.patBirthday || formData.patBirthday || "";
            data["guardianName"] = hisData.guardianName || formData.guardianName || "";
            data["guardianIdNo"] = userType ? (hisData.guardianIdNo || formData.patIdNo) : "";
            data["guardianPhone"] = userType ? (hisData.guardianPhone || formData.patPhone) : "";
            return data;
        },
        validForm(values) {
            if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                this.$toast({
                    message: "",
                    forbidClick: false
                });
                return false;
            }
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    message: "",
                    forbidClick: false
                });
                return false;
            };
            return true;
        },
        closeAllPicker() {
            this.showDatePicker = false;
            this.showRelationPicker = false;
        },
        toastFail(msg) {
            this.btnDisabled = false;
            this.$toast({
                message: msg,
                forbidClick: false
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onClose: () => {
                    this.$router.go(this.sociaSecuCard ? -2 : -1);
                }
            })
        }
    }
};
</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.user-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.user-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
.section {
    margin-top: 10px;
}
.tips {
    padding: 20px 0 30px 0;
    color: #494D5A;
    font-size: 12px;
    text-align: left;
}
.normal {
    line-height: 20px;
}
.danger {
    color: #FF6257;
}
.confirm-btn {
    height: 43px;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}

</style>



