const methods = {
    getDate(add = 0) {
        let currDate = new Date();
        let newDateMs = currDate.getTime() + add*86400000;
        let newDate = new Date(newDateMs);
        let year = newDate.getFullYear() + "";
        let month = newDate.getMonth() + 1;
        month = (month + "").length == 1 ? ("0" + month) : (month + "");
        let day = newDate.getDate();
        day = (day + "").length == 1 ? ("0" + day) : (day + "");
        let weekNum = newDate.getDay();
        let week = "";
        switch (weekNum) {
            case 0:
                week = "";
                break;
            case 1:
                week = "";
                break;
            case 2:
                week = "";
                break;
            case 3:
                week = "";
                break;
            case 4:
                week = "";
                break;
            case 5:
                week = "";
                break;
            case 6:
                week = "";
                break;
        };
        return {year, month, day, week};
    },
    setCookie(name, value, time) {
        //s2020
        //h12h12
        //d30d30
        if (time) {
            var strsec = this.getsec(time);
            var exp = new Date();
            exp.setTime(exp.getTime() + strsec*1);
            document.cookie = name + "=" + escape (value) + ";expires=" + exp.toGMTString();
        }
        else {
            document.cookie = name + "=" + escape (value);
        }
    },
    getsec(str) {
        let str1 = str.substring(1, str.length)*1; 
        let str2 = str.substring(0, 1); 
        if (str2 == "s") {
            return str1*1000;
        }
        else if (str2 == "h") {
            return str1*60*60*1000;
        }
        else if (str2 == "d") {
            return str1*24*60*60*1000;
        }
    },
    getCookie(name) {
        var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
        if (arr = document.cookie.match(reg)) {
            return unescape(arr[2]);
        }
        else {
            return null;
        }
    },
    delCookie(name) {
        var exp = new Date();
        exp.setTime(exp.getTime() - 1);
        var cval = this.getCookie(name);
        if (cval != null) {
            document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
        }
    },
    limitFloat(val) {
        let sNum = val.toString(); //
        if (sNum.indexOf('.') == 0) {// .
            sNum = '0' + sNum
        }
        sNum = sNum.replace(/[^\d.]/g,"");  //.
        sNum = sNum.replace(/\.{2,}/g,"."); //. 
        sNum = sNum.replace(".","$#$").replace(/\./g,"").replace("$#$",".");
        sNum = sNum.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//
        // 0102
        if(sNum.indexOf(".")< 0 && sNum !="") {
            sNum = parseFloat(sNum);
        }
        return sNum
    },
    wxPay(resData, fail) {
        function onBridgeReady() {
            WeixinJSBridge.invoke(
                'getBrandWCPayRequest',
                {
                    "appId": resData.appId,//     
                    "timeStamp": resData.timeStamp,//1970
                    "nonceStr": resData.nonceStr, //     
                    "package": resData.package,     
                    "signType": resData.signType,//     
                    "paySign": resData.paySign // 
                },
                function(res) {
                    if (res.err_msg == "get_brand_wcpay_request:ok") {
                        //  
                        document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
                        //   ios
                        WeixinJSBridge.call('closeWindow');
                    }
                    else {
                        fail && fail();
                    }
                }
            );
        };
        // 
        if (typeof WeixinJSBridge == "undefined") {
            fail && fail();
            if (document.addEventListener) {
                document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
            }
            else if (document.attachEvent) {
                document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
                document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
            }
        }
        else {
            onBridgeReady();
        }
    },
    aliPay(resData) {
        function ready(callback) {
            // jsbridge
            if (window.AlipayJSBridge) {
                callback && callback();
            } else {
                // 
                document.addEventListener('AlipayJSBridgeReady', callback, false);
            }
        };
        ready(function(){
            AlipayJSBridge.call("tradePay", {
                orderStr: resData.strPara
            }, function(result) {
                AlipayJSBridge.call('closeWebview');
            });
        });
    },
    
}

export default methods;


